.header {
  background-color: #1e1c1cbd;
  width: 100%;
  padding: 0.5rem 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  font-family: "Lobster", cursive;
}

.logo {
  width: 15rem;
  display: flex;
  color: #fff;
  align-items: center;
  justify-content: center;
  font-size: 26px;

  &__image {
    width: 25%;
  }
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  &__input {
    width: 100%;
    height: 37px;
    border-radius: 10px;
    outline: transparent;
    border: transparent;
    padding: 0 1rem;
    font-family: "Lobster", cursive;
    word-spacing: 2px;
    &::placeholder {
      color: #1e1c1c90;
    }
    &:focus {
      border: 2px solid rgb(46, 11, 11);
    }
  }
}

.register-login {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 85%;
  cursor: pointer;
  color: #fff;
  position: relative;
}



//Logout
.login__icon {
  width: 36px;
  height: 36px;
  background: olivedrab;
  border-radius: 50%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

//User Menu
.user-menu {
  position: absolute;
  z-index: 3;
  background-color: #e4e4e4;
  width: 125px;
  height: 100px;
  top: 94%;
  right: -52px;
  border-radius: 5px;
  opacity: 0;
  visibility: hidden;
  &_list {
    list-style: none;
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 12px;
    color: #525252e3;
    font-weight: 600;
    &_item {
      display: flex;
      justify-content: flex-start;
      padding: 7px 10px;
      width: 100%;
      align-items: center;
      & svg {
        margin-right: 5px;
      }
    }
  }
}
.register-login:hover .user-menu{
  opacity: 1;
  visibility: visible;
}
